.adcmdr-ad .adcmdr-inprogress {
  opacity: 0.5;
  pointer-events: none;
}
.adcmdr-ad img {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.adcmdr-ad-resp img {
  max-width: 100%;
  height: auto;
}

.adcmdr-ad-label {
  font-size: 0.8rem;
  margin-bottom: 5px;
  text-align: center;
}

.adcmdr-rotate {
  position: relative;
}
.adcmdr-rotate .adcmdr-ad {
  transition: opacity 250ms ease-in-out;
  display: none;
  position: relative;
  opacity: 0;
  top: 0;
  left: 0;
}
.adcmdr-rotate .adcmdr-ad.woactive {
  display: block;
  position: relative;
  opacity: 1;
}
.adcmdr-rotate .adcmdr-ad.adcmdr-amp-ad {
  display: block;
  opacity: 1;
}
.adcmdr-rotate .adcmdr-ad.adcmdr-amp-ad + .adcmdr-amp-ad {
  margin-top: 0.75rem;
}

.align-self-center {
  align-self: center;
}

.adcmdr-fr {
  float: right;
}

.adcmdr-fl {
  float: left;
}

.adcmdr-clr {
  clear: both;
  display: block;
  float: none;
}

.adcmdr-needs-consent {
  display: none;
}